@tailwind base;
@tailwind components;
@tailwind utilities;

html {
  font-family: 'Inter', 'Pretendard', 'Segoe UI', 'Roboto', 'Arial', sans-serif;
}
body {
  @apply bg-darkbg-900 text-white transition-colors duration-500;
  background: #111;
  min-height: 100vh;
}

.glass-card {
  @apply bg-darkbg-800/80 backdrop-blur-xs rounded-2xl border border-darkbg-700;
}

.btn-main {
  @apply bg-accent-blue text-white font-bold px-4 py-2 rounded-xl hover:bg-accent-blue/80 transition-all duration-200;
  box-shadow: none;
  border: none;
}

.input-dark {
  @apply bg-darkbg-700 border-darkbg-600 text-white placeholder-accent-gray rounded-lg px-3 py-2 focus:outline-none focus:ring-2 focus:ring-accent-blue focus:border-transparent transition-all duration-200;
}

::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: #181818;
}
::-webkit-scrollbar-thumb {
  background: #232323;
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: #3a3a3a;
}

.chat-bubble-ai {
  @apply bg-darkbg-700 text-white px-4 py-2 rounded-2xl mb-2 border border-accent-blue/30;
  box-shadow: none;
}
.chat-bubble-user {
  @apply bg-darkbg-600 text-white px-4 py-2 rounded-2xl mb-2 border border-darkbg-500/60;
  box-shadow: none;
}

.btn-primary {
  @apply bg-accent-blue text-white px-4 py-2 rounded-lg hover:bg-accent-blue/80 transition-colors font-bold;
}
.btn-secondary {
  @apply bg-darkbg-700 text-white px-4 py-2 rounded-lg hover:bg-darkbg-600 transition-colors;
}
.input-field {
  @apply input-dark;
}

.focus-ring:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
  box-shadow: 0 0 0 2px #3b82f6;
} 